import { Badge, BadgeVariant } from '../Badge/Badge';
import { getStandardPlanNames } from '../helpers/plans';

interface PlanBadgeProps {
  plan: string;
  href?: string | undefined;
  children?: JSX.Element | string | undefined;
  className?: string | undefined;
}

export const PlanBadge = ({
  plan,
  href = '',
  children = undefined,
  className = '',
}: PlanBadgeProps) => {
  const standardPlans = getStandardPlanNames().map((p) => p.toLowerCase());
  const highlight = !standardPlans.includes(plan.toLowerCase());
  return (
    <Badge
      variant={highlight ? BadgeVariant.Highlight : undefined}
      href={href}
      className={className}
    >
      {children || plan}
    </Badge>
  );
};
