// Detect OS-specific modifier key for Commandbar
// @see https://github.com/tryfoobar/commandbar-launcher/blob/df383795b72946be355ba147e900e3b3b531aed2/src/components/os.js
const os = () => {
  // If SSR or window-less environment, default to MAC
  if (typeof window === 'undefined') {
    return 'MAC';
  }

  const userAgent = window.navigator.userAgent;
  const platform = window.navigator.platform;
  const macosPlatforms = ['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'];
  const windowsPlatforms = ['Win32', 'Win64', 'Windows', 'WinCE'];
  const iosPlatforms = ['iPhone', 'iPad', 'iPod'];

  // default to Mac
  let os = 'MAC';

  if (macosPlatforms.indexOf(platform) !== -1) {
    os = 'MAC';
  } else if (iosPlatforms.indexOf(platform) !== -1) {
    os = 'IOS';
  } else if (windowsPlatforms.indexOf(platform) !== -1) {
    os = 'WINDOWS';
  } else if (/Android/.test(userAgent)) {
    os = 'ANDROID';
  } else if (/Linux/.test(platform)) {
    os = 'LINUX';
  }

  return os;
};

export const osControlKey = () => {
  if (['WINDOWS', 'ANDROID', 'LINUX'].includes(os())) {
    return 'Ctrl';
  }
  return '⌘';
};

export const isProbablyMobileDevice = () => {
  if (
    /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
      navigator.userAgent
    )
  ) {
    return true;
  }
  return false;
};
