import { Avatar, type AvatarUser } from '../Avatar/Avatar';
import { Dropdown, type IDropdownItem } from '../Dropdown/Dropdown';
import { cn } from '../helpers/cn';

interface UserDropdownProps {
  user?: AvatarUser;
  className?: string;
  onLogout?: () => void;
  alignRight?: boolean;
  userPath: string;
  links?: IDropdownItem[];
  showUserDetails?: boolean;
  menuPosition?: 'above' | 'below';
  isNavCollapsed?: true | undefined;
}

export function UserDropdown({
  user,
  className,
  onLogout,
  userPath,
  alignRight = true,
  links = [],
  showUserDetails = false,
  menuPosition = 'below',
  isNavCollapsed,
}: UserDropdownProps) {
  if (!user) {
    return null;
  }

  const { full_name: name = '', email = '' } = user || {};

  const dropdownOptions = [
    {
      label: (
        <>
          {/* Data attributes are used for identifying the user in the Netliusers extension */}
          <strong data-user-name={name} className="h4 tw-block">
            {name}
          </strong>
          <small data-user-email={email} className="meta">
            {email}
          </small>
        </>
      ),
      to: userPath,
    },
    ...links,
    {
      label: 'Sign out',
      onClick: onLogout,
      className: 'danger',
    },
  ].filter(Boolean);

  return (
    <Dropdown
      alignRight={alignRight}
      className={className}
      noCaret
      noCheck
      variant="bare"
      toggle={{
        ariaLabel: `User ${email}`,
        activeClassName: 'is-active',
        className: cn(
          'tw-group !tw-bg-transparent',
          showUserDetails
            ? 'tw-flex tw-w-full tw-max-w-full tw-flex-row tw-gap-x-2 tw-overflow-hidden tw-px-1 tw-leading-4'
            : 'tw-relative tw-size-9 tw-p-0 tw-leading-none'
        ),
        label: (
          <>
            <span className="tw-relative tw-flex tw-size-[34px] tw-max-w-full tw-shrink-0 tw-items-center tw-rounded-full tw-text-transparent tw-ring-[1.5px] tw-ring-[currentColor] tw-transition-colors tw-duration-150 group-hover:tw-text-teal-action group-[.is-active]:tw-text-teal-action dark:group-hover:tw-text-teal-action dark:group-[.is-active]:tw-text-teal-action">
              <Avatar user={user} dropdown />
            </span>
            <span
              className={cn(
                showUserDetails
                  ? cn(
                      'tw-flex tw-max-w-[calc(100%-34px-theme(spacing.2))] tw-flex-col tw-self-center tw-text-left',
                      'tw-ease-in-out tw-transition-opacity',
                      isNavCollapsed
                        ? 'tw-opacity-0 tw-duration-50'
                        : 'tw-opacity-100 tw-delay-200 tw-duration-150'
                    )
                  : 'tw-sr-only'
              )}
            >
              {name && (
                <span
                  className={cn(
                    showUserDetails &&
                      'tw-relative tw-top-px tw-mb-[2px] tw-w-full tw-truncate tw-text-sm tw-font-semibold tw-text-gray-darker dark:tw-text-gray-lighter',
                    !showUserDetails && 'tw-sr-only'
                  )}
                >
                  {name}
                </span>
              )}
              <span
                className={cn(
                  'tw-w-full tw-truncate tw-text-xs tw-font-regular tw-text-gray-darker dark:tw-text-gray-lighter',
                  !showUserDetails && 'tw-sr-only'
                )}
              >
                {email}
              </span>
            </span>
          </>
        ),
      }}
      menu={{
        className: cn(menuPosition === 'below' && 'tw-top-10 tw-mt-1'),
      }}
      menuPosition={menuPosition}
      items={dropdownOptions}
    />
  );
}
