import { cn } from '../helpers/cn';
import type { Props as ButtonProps } from './Button';
import type { ButtonIconBasicProps } from './ButtonIcon';

interface ButtonCNProps
  extends Partial<
      Pick<
        ButtonProps,
        | 'level'
        | 'variant'
        | 'saving'
        | 'disabled'
        | 'outset'
        | 'className'
        | 'size'
      >
    >,
    Partial<Pick<ButtonIconBasicProps, 'callForAttention'>> {
  type?: 'default' | 'icon';
}

export const buttonCN = ({
  type = 'default',
  level = 'primary',
  variant = 'standard',
  size = 'default',
  saving,
  disabled,
  callForAttention,
  outset,
  className,
}: ButtonCNProps): string =>
  cn(
    'btn',
    `btn-${type}`,
    type === 'default' && `btn--size-${size}`,
    `btn-${level}`,
    `btn-${level}--${variant}`,
    saving && `btn--saving btn-${level}--saving`,
    disabled && `btn--disabled btn-${level}--disabled`,
    callForAttention && 'btn--call-for-attention',
    outset,
    className
  );
